@import "../../styles/common";

.sec-heading {
  padding: 1rem 0;
  border-bottom: none !important;
  margin-bottom: 3rem;
  font-family: $neue-helvetica-bold;
  font-size: 2rem;
  &.showall-heading {
    display: flex !important;
    justify-content: space-between;
    padding: 0.5rem 0;
    border-bottom: none !important;
    border-bottom: none !important;
    font-family: "neue_helvetica_medium" !important;
    font-style: italic;
  }
  @media screen and (max-width: 480px) {
    flex-wrap: wrap;
  }
  .filt-flex-search {
    justify-content: space-between;
  }
}

.showall-nft-section {
  padding: 5rem 0;
  background-color: #fff;
  background: transparent linear-gradient(180deg, #073824 0%, #410021 100%) 0%
    0% no-repeat padding-box;
  color: #fff;
}

button.load_more {
  text-decoration: none;
  border-radius: 2em;
  color: #fff;
  border: 0.2rem solid #000;
  margin-left: 0;
  margin-top: 2rem;
  display: inline-block;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
  font-family: $neue-helvetica-bold;
  width: auto;
  padding: 0.8rem 4rem;
  font-weight: normal;
  background: #000;
  &:hover,
  &:focus,
  &:active {
    background: #fff;
    color: #000;
    border: 0.2rem solid #000;
  }
}

.showall-filter-blocks {
  @media screen and (max-width: 991px) {
    flex-direction: column;
  }
  &.days {
    margin-top: 0rem !important;
    .dropdown {
      width: initial !important;
    }
  }
  @media screen and (max-width: 767px) {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: relative;
  }

  @media screen and (max-width: 425px) {
    margin-top: 0.5rem !important;
  }
  .filter-box {
    @media screen and (max-width: 991px) {
      margin-top: 0.5rem;
      width: 100%;
      justify-content: space-between;
    }

    .dropdown {
      margin-right: 0.5rem;

      @media screen and (max-width: 991px) {
        width: 24%;
        margin-right: 0rem;
      }
      @media screen and (max-width: 425px) {
        width: 49%;
      }
      &.price-range {
        @media screen and (max-width: 767px) {
          width: 100%;
        }
      }
    }
  }
  .dropdown-menu {
    max-height: 320px;
    overflow-y: auto;
    position: absolute !important;
    inset: 0px auto auto 0px !important;
    transform: translate3d(0px, 24px, 0px) !important;
    background: #0d3624;
    border: 1px solid #e1ff04;

    button {
      color: #fff;

      &:hover {
        background: #fff;
      }
    }

    .category-search-block {
      position: relative;
      display: block;
      .category-search-icon {
        position: absolute;
        top: 0.5rem;
        right: 0.5rem;
      }
      .category-search.form-control {
        height: 2.3rem;
        width: 100%;
        border-radius: 0.2rem;
        box-shadow: none !important;
        // margin-top: 0.5rem;
        &:hover,
        &:focus,
        &:active {
          border-color: #000 !important;
        }
      }
    }

    .scroll-fixed {
      max-height: 240px;
      overflow: auto;
      margin: 0.5rem 0;
    }
  }
}
.filter-drop-btn {
  padding: 0.2rem 0.8rem 0.2rem 0.8rem;
  font-size: 0.8rem;
  font-family: $neue-helvetica-medium;
  // border: 1px solid #2c5154;
  border: 1px solid #e1ff04;
  border-radius: 50rem;
  margin-bottom: 0.2rem;
  cursor: pointer;
  white-space: nowrap;
  font-style: italic;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
  @media screen and (max-width: 767px) {
    padding: 0.2rem 0.3rem 0.2rem 1rem;
  }
}
.filter-drop-btn + .dropdown-menu .dropdown-item {
  display: flex;
  svg {
    height: 1.2rem;
    width: 1.2rem;
    min-height: 1.2rem;
    min-width: 1.2rem;
  }
}

.filter-drop-sort-btn {
  padding: 0.2rem 0.8rem 0.2rem 0.8rem;
  font-size: 0.8rem;
  font-family: $neue-helvetica-medium;
  // border: 1px solid #2c5154;
  border: 1px solid #e1ff04;
  border-radius: 50rem;
  margin-bottom: 0.2rem;
  cursor: pointer;
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-style: italic;
  text-transform: uppercase;
}

.filter-pill-button {
  // background-color: rgb(231, 231, 231);
  // border: 1px solid #ccc;
  // padding: 0.2rem 0.2rem 0.2rem 0.8rem;
  // border-radius: 50rem;
  // display: flex;
  // margin-right: 0.5rem;
  // margin-bottom: 0.5rem;
  padding: 0 0.5rem 0 1rem;
  display: flex;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
  color: #fff;
  align-items: center;
  justify-content: center;
  height: 2rem;
  background: #073824 0% 0% no-repeat padding-box;
  border: 1px solid #2c5154;
  border-radius: 50rem;
  opacity: 1;
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(30px);
  text-transform: uppercase;
  font-size: 0.8rem;

  .first {
    font-family: $neue-helvetica-medium;
  }

  &:hover {
    background-color: #fff;
    border: 1px solid rgb(129, 129, 129);
    color: #000;
  }

  .last {
    &:hover {
      color: rgb(153, 26, 26);
    }
  }
}

