.filt-flex-box {
  display: flex;

  @media screen and (max-width: 991px) {
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  @media screen and (max-width: 640px) {
    flex-direction: column;
  }
  .dropdown {
    margin-right: 0.5rem;
    @media screen and (max-width: 991px) {
      width: 49%;
      margin-right: 0rem;
    }
    @media screen and (max-width: 640px) {
      width: 100%;
    }
  }
  @media screen and (max-width: 425px) {
    width: 100%;
    .dropdown {
      width: 100%;
      .filter-drop-sort-btn {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
    }
  }
}
.explore-all-text {
  font-size: 1.5rem;
  font-style: normal !important;
  cursor: pointer;
  span {
    font-size: 1.2rem;
    background: transparent;
    color: #fff;
    padding: 0.5rem 0.2rem;
    border-radius: 50rem;
    text-decoration: none;
    outline: 0;
    border: 0.2rem solid transparent;
    cursor: pointer;
    font-style: italic;
  }
  @media screen and (max-width: 991px) {
    .explore-all-text {
      font-size: 1rem;
    }
  }
}
.filt-flex-search {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-left: 0.5rem;
  width: 100%;
  max-width: 25rem !important;
  @media screen and (max-width: 991px) {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-left: 0.5rem;
    width: 100%;
    max-width: 25rem;
  }

  .search-box-add {
    border-radius: 50rem;
    padding: 0.5rem 1rem;
    height: 2.5rem;
    font-size: 1rem !important;
    font-style: italic;
    font-family: $neue-helvetica-bold;
    // border: none;
    border: 1px solid #e1ff04;
    color: #fff;
    margin-left: 0;
    outline: none;
    width: 100%;
    padding-right: 2rem;
    // background: #ffffff24 0% 0% no-repeat padding-box;
    background: transparent;
    border-radius: 18px;
    opacity: 1;
    backdrop-filter: blur(30px);
    -webkit-backdrop-filter: blur(30px);
  }
}
.search {
  &-box-add {
    border-radius: 50rem;
    padding: 0.5rem 1rem;
    height: 2.5rem;
    border: 1px solid #000;
    margin-left: 0;
    outline: none;
    width: 100%;
    padding-right: 2.5rem;
    &::placeholder {
      color: #8f8f8f;
      font-size: 1rem;
    }
  }
  &-button {
    border-radius: 50rem;
    margin-left: 0.2rem;
    height: 2.5rem;
    width: 2.5rem;
    display: flex;
    padding: 0 !important;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 0.2rem !important;
    top: initial !important;
    bottom: 0;
    svg {
      height: 1.5rem !important;
      width: 1.5rem !important;
    }
  }
}

.bot-border-hr {
  border-bottom: 1px solid #ccc !important;
}

.prifilter-btn {
  display: flex;
  justify-content: space-between;
  button {
    width: 49%;
    border-radius: 50rem !important;
    &.apply-btn {
      background: #000;
      color: #fff;
      &:disabled {
        background-color: #e7e7e9;
        color: #9c9c9b;
        border: none;
      }
    }
  }
}

.showall-filter-blocks {
  padding: 3rem 0;
  background: #0000008f 0% 0% no-repeat padding-box;
  opacity: 1;
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(30px);
  position: relative;
  z-index: 5;
}
