@import "../../styles/common";

.nft-more {
  position: relative;

  .title {
    font-size: 1.5rem;
    font-family: $neue-helvetica-medium;
    margin-bottom: 1rem;
    position: relative;

    .title-count {
      font-size: 1rem;
      font-family: $neue-helvetica-bold;
      top: 0;
      width: 0;
      position: absolute;
      margin-left: 0.2rem;
    }
  }
  .nft-more-content {
    display: flex;
    flex-wrap: nowrap;
    overflow: hidden;
    scroll-behavior: smooth;
    font-family: $neue-helvetica-medium;
    min-width: 100%;

    .more-card {
      margin-right: 1rem;
      margin-bottom: 2rem;
      min-width: 100%;
      max-width: 100%;
      display: flex;
      flex-direction: column;
      position: relative;
      text-decoration: none;
      color: #000;

      &:hover {
        color: #000;
      }

      .nft-like-btn {
        position: absolute;
        right: 10px;
        top: 10px;
        background-color: rgba(0, 0, 0, 0.39);
        padding: 0.6rem;
        border-radius: 0.5rem;
        cursor: pointer;

        &:hover {
          background-color: rgba(0, 0, 0, 0.719);
        }
      }

      .nft-type-badge {
        position: absolute;
        top: 0.2rem;
        right: 0;
        background: #000;
        color: #fff;
        padding: 0;
        font-size: 1rem;
        border-radius: 20px 0 0 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 2.5rem;
        min-width: 7rem;
        padding: 0 1.5rem;
        font-family: $neue-helvetica-bold;
        box-shadow: 1px 1px 0px 1px #ffff;
      }
      @media screen and (max-width: 559px) {
        min-width: 100%;
        max-width: 100%;
      }
      @media screen and (min-width: 560px) {
        min-width: calc(50% - 1rem);
        max-width: calc(50% - 1rem);
      }
      @media screen and (min-width: 992px) {
        min-width: calc(33.3333% - 1rem);
        max-width: calc(33.3333% - 1rem);
      }
      @media screen and (min-width: 1024px) {
        min-width: calc(25% - 1rem);
        max-width: calc(25% - 1rem);
      }

      &:last-child {
        margin-right: 0;
      }

      overflow: hidden;

      img {
        min-width: 100%;
        height: 26rem;
        object-fit: contain;
        margin-bottom: 1rem;
      }

      .top-content-title {
        display: flex;
        justify-content: space-between;
        flex-direction: column;

        .more-nft-title {
          font-size: 1.3rem;
          font-family: $neue-helvetica-bold;
          position: relative;
          display: block;
          @include text-ellipsis;

          .right-content {
            width: calc(100% - 8rem);
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }

          .left-count {
            // position: absolute;
            color: #f21e00;
            right: 0;
            font-family: $neue-helvetica-bold;
            font-size: 1rem;
            top: 0.5em;
            width: 8rem;
            display: flex;
            align-items: center;
            justify-content: flex-end;
          }
        }
        .more-nft-desc {
          font-size: 1rem;
          font-family: $neue-helvetica-medium;
          margin-bottom: 0.5rem;
        }
      }

      .more-bid-details {
        display: flex;
        justify-content: space-between;

        .mb-title {
          font-size: 1rem;
          font-family: $neue-helvetica-medium;
          white-space: nowrap;
          display: flex;
          align-items: center;
          .value-diff-range {
            font-family: $neue-helvetica-medium;
            font-size: 0.9rem;
            margin-top: 0rem;
            display: inline-flex;
            margin-left: 0.3rem;
            align-items: center;
            .arrow-icon {
              font-size: 0.9rem;
            }
            &.green {
              color: #00a954;
            }

            &.red {
              color: #f21e00;
            }
          }
        }

        .mb-title-date {
          font-size: 1rem;
          font-family: $neue-helvetica-medium;
          white-space: nowrap;
        }

        .mb-value {
          font-size: 1.2rem;
          font-family: $neue-helvetica-bold;
        }
      }

      // &:hover {
      //   opacity: 0.85;
      // }
    }
  }

  .chevron-left-nav {
    position: absolute;
    top: 40%;
    background-color: white;
    padding: 0.7rem;
    outline: none;

    z-index: 1;

    border-radius: 50%;
    border: 1px solid #ccc;
    left: 0;

    svg {
      height: 30px;
      width: 33px;
    }

    &:hover {
      opacity: 1;
      box-shadow: 0 0 6px 0px #dadada;
    }
  }

  .chevron-right-nav {
    position: absolute;
    top: 40%;
    background-color: white;
    border: 1px solid #ccc;
    padding: 0.7rem;
    outline: none;

    z-index: 1;

    border-radius: 50%;
    right: 0;

    svg {
      height: 30px;
      width: 33px;
    }

    &:hover {
      opacity: 1;
      box-shadow: 0 0 6px 0px #dadada;
    }
  }
}
