.nft-more .nft-more-content {
  .more-card.jt-card {
    padding: 0.2rem;
    position: relative;
    border-radius: 0.3rem;
    background: transparent
      linear-gradient(180deg, #e2ff00 0%, #18e0e0 47%, #e8318d 100%) 0% 0%
      no-repeat padding-box;
    height: calc(100% - 2rem);
    &::before {
      content: "";
      position: absolute;
      background: #000;
      top: 0.2rem;
      bottom: 0.2rem;
      left: 0.2rem;
      right: 0.2rem;
      z-index: 0;
      border-radius: 0.3rem;
    }
    .player_stats {
      position: absolute;
      top: 1rem;
      right: 1rem;
      z-index: 4;
      border: none;
      border-radius: 50rem;
      box-shadow: inset 0px 0px 3rem 1.5rem #292929 !important;
      .player-type {
        padding: 0.6rem 0.4rem;
        border-bottom: 0.1rem solid #fff;
        img {
          display: flex;
          max-height: unset !important;
          object-fit: contain;
          object-position: center;
          padding: 0.1rem;
          min-width: 3.5rem;
          min-height: 3.5rem;
          background: #000;
          width: 3.5rem;
          height: 3.5rem;
          margin-top: 0.5rem;
          margin-bottom: 0.5rem;
          border-radius: 50rem;
          margin-left: auto;
          margin-right: auto;
        }
      }
      .player-range {
        padding: 0.6rem 0.4rem;
        // border-bottom: 0.1rem solid #fff;
        .band {
          font-size: 1rem;
          padding: 0.1rem 1rem;
          margin-bottom: 0.5rem;
          display: flex;
          align-items: center;
          justify-content: center;
          font-family: $neue-helvetica-extended-bold;
          letter-spacing: 0.08rem;
          line-height: 1.3;
          border-radius: 15px;
          // background: #803cef;
        }
      }
      .player-level {
        padding: 0.6rem 0.4rem;
        img {
          display: flex;
          max-width: 3rem;
          max-height: unset !important;
          object-fit: contain;
          object-position: center;
          padding: 0.2rem;
          width: 100%;
          height: 3rem;
        }
        h6 {
          font-size: 1rem;
          font-family: $neue-helvetica-extended-bold;
          text-transform: uppercase;
          letter-spacing: 0.08rem;
          text-align: center;
          margin-bottom: 0;
          color: #fff;
        }
      }
    }
    img {
      position: relative;
      z-index: 1;
    }
    .top-content-title {
      position: relative;
      padding: 1rem;
      .more-nft-title {
        color: #fff;
        font-family: $neue-helvetica-extended-bold;
        letter-spacing: 0.08rem;
      }
      .more-nft-desc {
        color: #fff;
        font-family: $neue-helvetica-light;
        letter-spacing: 0.08rem;
        white-space: nowrap;
        width: 100%;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .heart_box {
        position: absolute;
        z-index: 2;
        height: 3rem;
        width: 3rem;
        border-radius: 50rem;
        right: 1rem;
        top: -3rem;
        .svg_size {
          height: 3rem;
          width: 3rem;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 0.5rem;
          border: 0.1rem solid #fff;
          color: #fff !important;
          margin-bottom: 0.5rem;
          border-radius: 50rem;
          cursor: pointer;
          background-size: 1.4rem;
          background-position: center;
          background-repeat: no-repeat;

          &.heart_icon {
            border: 0.1rem solid #e8318d;
            box-shadow: inset 0px 0px 20px #e8318d !important;
            background-image: url("../../images/jump-trade/heart_ico.png");
          }
          &.filled_heart_icon {
            border: 0.1rem solid #e8318d;
            box-shadow: inset 0px 0px 20px #e8318d !important;
            background-image: url("../../images/jump-trade/heart-filled.png");
          }
        }
      }
    }

    .more-bid-details {
      margin-top: 1.5rem;
      .mb-title {
        color: #fff !important;
        font-family: $neue-helvetica-light;
        font-size: 0.9rem;
        letter-spacing: 0.08rem;
      }
      .mb-value {
        color: #fff;
        font-family: $neue-helvetica-extended-bold;
        font-size: 1rem;
      }
    }

    .nft-type-badge {
      position: absolute;
      top: 1rem;
      right: initial;
      left: 0.8rem;
      background: #e8318d;
      color: #fff;
      font-size: 1rem;
      border-radius: 0.3rem;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 2.5rem;
      min-width: 7rem;
      padding: 0 1.5rem;
      font-family: $neue-helvetica-extended-bold;
      box-shadow: none;
      z-index: 2;
    }

    .time-counter-box {
      position: absolute;
      left: 1rem;
      right: initial;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: none;
      background: #000;
      border-radius: 50rem;
      color: #fff;
      top: -3rem;
      flex-direction: row;
      background: transparent linear-gradient(94deg, #e2ff00 26%, #08ff0b 100%)
        0% 0% no-repeat padding-box;
      .time-counter-card {
        display: flex;
        align-items: flex-start;
        justify-content: center;
        flex-direction: column;
        position: relative;
        z-index: 1;
        color: #000;
        padding: 0.2rem 1rem 0.2rem 1rem;
        border-radius: 50rem;
        background: transparent
          linear-gradient(94deg, #e2ff00 26%, #08ff0b 100%) 0% 0% no-repeat
          padding-box;

        span.time-title,
        .nft-counter {
          line-height: 1;
        }
      }
    }
  }
}
